import axios from 'axios'

 const actions = {
    newsData: ({commit}) => {
        
        axios.get('https://gouapp.herokuapp.com/news')
        .then(resp =>{
            //console.log(resp.data.borditem.comments)
            
            let posts = resp.data.news;
            commit('SET_NEWS',posts)
        })
        .catch(err=>console.log(err));
       }
}

export default actions